.element__delete {
	position: absolute;
	top: 18px;
	right: 16px;
	background-image: url(../../../images/DeleteButton/delete.svg);
	width: 18px;
	height: 19px;
	margin: 0;
	border: none;
	cursor: pointer;
	background-color: rgba(0, 0, 0, 0);
	transition: opacity .1s;
}

.element__delete:hover {
	cursor: pointer;
	opacity: .6;
}